@import "../../_globalColor";

.meter {
  height: 20px;
  position: relative;
  background: $progressBarBackground;
  -moz-border-radius: 25px;
  -webkit-border-radius: 25px;
  border-radius: 25px;
}

.meter>span {
  display: block;
  height: 100%;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: $progressBarSpanBG;
  position: relative;
  overflow: hidden;
  transition: width 1s ease-in-out; // Added for smooth animation on hover
}

.skill {
  line-height: 3.5vw;
}

.skill-bars {
  font-size: 28px;
  width: 40%;
}

.skills-container {
  display: flex;
  width: 55%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 6rem;
  margin-left: 20%;
}

.skills-container>* {
  flex: 1;
  margin-bottom: 30px;
}

/* Media Query */
@media (max-width: 1456px) {
  .skills-bar {
    line-height: 4rem;
  }

}

@media (max-width: 768px) {
  .skills-container {
    flex-direction: column;
  }

  .skills-bar {
    line-height: 3rem;
    margin: 20px;
  }

}